import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Biaya } from 'src/app/sdk/hr/models/biaya';
import { GaBiayaService, GaBiayaRincianService, DirektoriVPegawaiPosisiService, GaUntukService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core'
import { V_pegawai_posisi, Projek, Pegawai, Untuk, Projek_anggota } from 'src/app/sdk/hr/models'
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {
    constructor(
        private gaBiayaService: GaBiayaService,
        private gaUntukService: GaUntukService,
        private gaBiayaRincianService: GaBiayaRincianService,
        private toolsLogFlowService: ToolsLogFlowService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private routerActive: ActivatedRoute,
        private router: Router,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private standarApprovalService: StandarApprovalService

    ) { }

    id_biaya: string = ''
    dataBiaya: Biaya = {}
    dataPegawai: Pegawai = {}
    dataPosisi: V_pegawai_posisi = {}
    dataUntuk: Untuk = {}
    dataRincian: Projek_anggota[] = []

    ngOnInit() {
        this.routerActive.paramMap.subscribe(
            params => {
                console.log(params.get('id_biaya'))
                if (params.get('id_biaya')) {
                    this.id_biaya = params.get('id_biaya')
                }

                this.getDetailBiaya()
                this.getRincianBiaya()
                this.getLog()
            }
        )



        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailBiaya()
                    this.getRincianBiaya()
                    this.getLog()
                }
            }
        )
    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getDetailBiaya() {
        this.spinner.show()
        this.gaBiayaService.getGaBiayasId(this.id_biaya).subscribe(
            data => {
                this.dataBiaya = data.data;
                this.getPegawai(data.data.id_pegawai);
                this.getPosisi(data.data.id_pegawai);
                this.getUntuk(data.data.id_untuk);

                this.params = {
                    id: data.data.id_biaya,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_biaya"
                }
                this.standarApprovalService.param.next(this.params)

            }
        )
    }


    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            data => {
                this.dataPosisi = data.data;
            }
        )
    }
    getUntuk(id_untuk: string) {
        this.gaUntukService.getGaUntuksId(id_untuk).subscribe(
            data => {
                this.dataUntuk = data.data;
            }
        )
    }


    getRincianBiaya() {
        this.gaBiayaRincianService.getGaBiayaRincians(JSON.stringify({ id_biaya: this.id_biaya })).subscribe(
            res => {
                this.dataRincian = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_biaya })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/ga/request/module/biaya/list'])
    }

}
